import React from 'react';

import logoImage from '../../assets/images/logo.png';
import classes from "./Logo.module.css";

const logo = (props) => {
    return (
        <div className={classes.Logo}>
            <img src={logoImage} alt="Burger Shack logo"/>
        </div>
    )
}
 
export default logo;


